export default{
	data(){
		return {
			name:"",
			url: "",
			bgUrl: ""
		}
	},
	onLoad: function(opting) {
		this.name = opting.name;
		uni.request({
			url: "http://localhost:3000/song/url",
			data: {
				id: opting.id
			},
			success: res => {
				this.url = res.data.data[0].url
			}
		})
		uni.request({
			url: "http://localhost:3000/song/detail",
			data:{ids: opting.id},
			success: res => {
				this.bgUrl = res.data.songs[0].al.picUrl
			}
		})
	},
	methods:{
	}
}
